import React from 'react'
import styled from 'styled-components'

const Li = styled.li`
  padding: 5px 20px;
  margin: 4px 1px;
  border: none;
  color: white;
  width: 100px;
  text-align: center;
  border-radius: 25px;
  list-style: none;
  transition: 0.2s;
  &:hover {
    transform: scale(1.1);
    box-shadow: 1px 1px 4px 3px rgba(0, 0, 0, 0.1);
  }
`

const Reason = ({ reason }) => {
  return (
    <Li style={{ background: `${reason.color}`, color: `${reason.color}` }}>
      {reason.name}
    </Li>
  )
}

export default Reason
